\t\t\t<span class=\"icon-loading-small\" />\n\t\t\t</template>\n\t\t</NcButton>\n\t\t<div v-if=\"systemInfo\" class=\"system-info\">\n\t\t\t<h3>{{ t('mediadc', 'System info') }}</h3>\n\t\t\t<NcButton v-if=\"systemInfo\"\n\t\t\t\ttype=\"tertiary\"\n\t\t\t\tclass=\"mediadc-button-vue\"\n\t\t\t\t@click=\"copySystemInfoToClipboard\">\n\t\t\t\t{{ t('mediadc', 'Copy to clipboard') }}\n\t\t\t\t<template #icon>\n\t\t\t\t\t<ContentCopy :size=\"16\" />\n\t\t\t\t</template>\n\t\t\t</NcButton>\n\t\t\t<p>\n\t\t\t\t<pre>{{ systemInfo }}</pre>\n\t\t\t</p>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport axios from '@nextcloud/axios'\nimport { generateUrl } from '@nextcloud/router'\nimport { showSuccess } from '@nextcloud/dialogs'\n\nimport NcButton from '@nextcloud/vue/dist/Components/NcButton.js'\nimport ContentCopy from 'vue-material-design-icons/ContentCopy.vue'\n\nexport default {\n\tname: 'BugReport',\n\tcomponents: {\n\t\tNcButton,\n\t\tContentCopy,\n\t},\n\tdata() {\n\t\treturn {\n\t\t\tsystemInfo: null,\n\t\t\tupdating: false,\n\t\t}\n\t},\n\tmethods: {\n\t\tcollectSystemInfo() {\n\t\t\tthis.updating = true\n\t\t\taxios.get(generateUrl('/apps/cloud_py_api/api/v1/system-info')).then(res => {\n\t\t\t\tthis.systemInfo = res.data\n\t\t\t\tthis.updating = false\n\t\t\t}).catch(err => {\n\t\t\t\tconsole.debug(err)\n\t\t\t\tthis.updating = false\n\t\t\t})\n\t\t},\n\t\tcopySystemInfoToClipboard() {\n\t\t\tnavigator.clipboard.writeText(JSON.stringify(this.systemInfo, null, 2))\n\t\t\tshowSuccess(this.t('cloud_py_api', 'Copied to clipboard'))\n\t\t},\n\t},\n}\n</script>\n\n<style scoped>\n.bug-report {\n\tpadding: 20px 0;\n\tborder-top: 1px solid var(--color-error);\n\tborder-bottom: 1px solid var(--color-error);\n}\n\n.system-info pre {\n\tborder: 1px solid var(--color-border);\n\tpadding: 10px 20px;\n\tmargin: 20px 0;\n\tborder-radius: var(--border-radius);\n}\n</style>\n"],"sourceRoot":""}]);
